#feed_container {
	text-align: center;
	width: 100%;
	float: left;
	padding: 10px;
}
#feed_sorting_container {
	margin-right: 10px;
	margin-top: 10px;
	float: right;
}
#feed_sorting_color {
	font-size: 20px;
	font-weight: bold;
	color: #002157;
}
#feed_sort_button {
	margin-left: 10px;
}
#back_button {
	margin-left: 10px;
	margin-top: 10px;
	border-radius: 50%;
	background-color: white;
	width: 50px;
	height: 50px;
	cursor: pointer;
}

